/**
 * @file
 *
 * Tidy Local Tasks custom JS.
 */

(function ($, Drupal) {
  "use strict";
  Drupal.behaviors.editTabMenu = {
    attach: function (context, settings) {
      $(document).ready(function () {
        const $tltMenu = $('.tlt-menu'),
          $arrow = $tltMenu.find('.show-hide'),
          $oliveroLocalTasksToggle = $tltMenu.find('.tabs__trigger');
        if ($tltMenu.length) {
          $arrow.once().click(function () {
            $tltMenu.toggleClass("active");
          });

          if ($oliveroLocalTasksToggle.length) {
            $oliveroLocalTasksToggle.remove();
          }
        }
      });
    },
  };
})(jQuery, Drupal);
